<template lang="pug">
.comp-car-filter
    .item
        label 品牌
        .right
            .choose-item.choosed 不限
            .choose-item(v-for="item in 30") 大众
        .all
            el-button(type="text") 全部
                i(class="el-icon-arrow-down el-icon--right")
    .item
        label 车系
        .right
            .choose-item.choosed 不限
            .choose-item(v-for="item in 30") 福克斯
        .all
            el-button(type="text") 全部
                i(class="el-icon-arrow-down el-icon--right")
    .item
        label 价格
        .right
            .choose-item.choosed 不限
            .choose-item 3万以下
            .choose-item 3-5万
            .my-define
                el-input(size="mini")
                .space -
                el-input(size="mini")
                el-button(type="primary" size="mini") 确定
        .all
            el-button(type="text") 全部
                i(class="el-icon-arrow-down el-icon--right")
    .item
        label 更多
        .right
            el-dropdown(split-button type="default" size="mini") 年限
                el-dropdown-menu(slot="dropdown")
                    el-dropdown-item 1年以内
                    el-dropdown-item 1-3年
                    el-dropdown-item 3-5年
            el-dropdown(split-button type="default" size="mini") 年限
                el-dropdown-menu(slot="dropdown")
                    el-dropdown-item 1年以内
                    el-dropdown-item 1-3年
                    el-dropdown-item 3-5年

        .all
            el-button(type="text") 全部
                i(class="el-icon-arrow-down el-icon--right")
    .item
        label
        .right
            el-button(type="primary" @click="startSearch") 开始搜索
            el-button(type="primary" @click="newCar") 新增车
</template>

<script>
export default {
    methods: {
        newCar () {
            this.$emit('new-car');
        },
        startSearch () {
            this.$emit('start-search');
        }
    }
};
</script>

<style lang="scss" scoped>
.comp-car-filter {
    border: 1px solid #ebeef5;
    margin-bottom: 20px;
    .item {
        display: flex;
        align-items: stretch;
        min-height: 50px;
        border-bottom: 1px solid #ebeef5;
        &:last-child {
            border-bottom: 0 none;
        }
        label {
            flex-shrink: 0;
            width: 70px;
            text-align: center;
            background-color: #f5f5f5;
            line-height: 50px;
            color: #666;
        }
        .right {
            flex-grow: 1;
            display: flex;
            align-items: center;
            flex-wrap: wrap;
            padding: 10px;
            .choose-item {
                margin: 5px 5px 5px 0;
                padding: 5px 7px;
                cursor: pointer;
                &.choosed {
                    margin-right: 10px;
                    background-color: #0cb25e;
                    color: #fff;
                }
            }
            .my-define {
                display: flex;
                align-items: center;
                margin-left: 10px;
                .el-input {
                    width: 60px;
                }
                .space {
                    padding: 0 10px;
                }
                .el-button {
                    margin-left: 10px;
                }
            }
            .el-dropdown {
                margin: 5px 10px 5px 0;
            }
        }
        .all {
            flex-shrink: 0;
            align-self: flex-end;
            margin: 0 10px;
            .el-button {
                color: #666;
                padding: 17px 0;
            }
        }
    }
}
</style>



